"use client";

import { useLayoutEffect, useRef } from "react";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export default function Philosophy() {
    const containerRef = useRef(null);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            // Headline split-text animation
            gsap.from(".philosophy-headline .char", {
                scrollTrigger: {
                    trigger: ".philosophy-headline",
                    start: "top 70%",
                },
                opacity: 0,
                x: -20,
                stagger: 0.02,
                duration: 0.6,
                ease: "power2.out",
            });

            // Body paragraphs
            gsap.from(".philosophy-text p", {
                scrollTrigger: {
                    trigger: ".philosophy-text",
                    start: "top 70%",
                },
                y: 30,
                opacity: 0,
                stagger: 0.2,
                duration: 0.8,
            });

            // Number counter
            gsap.from(".stat-number", {
                scrollTrigger: {
                    trigger: ".stat-number",
                    start: "top 80%",
                },
                textContent: 0,
                duration: 2,
                snap: { textContent: 1 },
                ease: "power1.out",
            });

        }, containerRef);

        return () => ctx.revert();
    }, []);

    return (
        <section ref={containerRef} className="py-32 bg-[--obsidian] border-t border-[--graphite]/30 relative overflow-hidden">

            {/* Subtle Grid Background */}
            <div className="absolute inset-0 opacity-5">
                <div className="w-full h-full" style={{
                    backgroundImage: "linear-gradient(var(--graphite) 1px, transparent 1px), linear-gradient(90deg, var(--graphite) 1px, transparent 1px)",
                    backgroundSize: "100px 100px"
                }} />
            </div>

            <div className="container-custom relative z-10">
                <div className="flex flex-col lg:flex-row gap-20">

                    {/* LEFT: 40% - Headline */}
                    <div className="lg:w-[40%]">
                        <div className="mb-8">
                            <span className="text-[--silver] text-xs uppercase tracking-[0.2em] font-mono-tech">
                                Our Approach
                            </span>
                        </div>

                        <h2 className="philosophy-headline font-display text-[clamp(2.5rem,5vw,3.5rem)] text-[--lime] leading-tight">
                            {"BUILDING FOR TOMORROW, NOT TODAY.".split("").map((char, i) => (
                                <span key={i} className="char inline-block">{char === " " ? "\u00A0" : char}</span>
                            ))}
                        </h2>
                    </div>

                    {/* RIGHT: 60% - Content */}
                    <div className="lg:w-[60%] space-y-12">

                        <div className="philosophy-text space-y-6 text-[clamp(1.125rem,2vw,1.25rem)] leading-relaxed">
                            <p className="text-[--silver]">
                                Most companies build incremental improvements to existing solutions.
                                10% faster. 15% cheaper. Slightly better UI.
                            </p>

                            <p className="text-[--ivory] font-medium">
                                We don't do that.
                            </p>

                            <p className="text-[--silver]">
                                We focus on problems that are emerging right now — problems that didn't exist two years ago.
                                Problems that will define the next decade.
                            </p>

                            <p className="text-[--silver]">
                                AI visibility tracking. Neural CRM systems. Intelligent automation that actually works.
                            </p>

                            <p className="text-[--ivory]">
                                We're a small team. We build one thing at a time. And we build it right.
                            </p>
                        </div>

                        {/* STAT - Products in Market */}
                        <div className="pt-12 border-t border-[--graphite]/30">
                            <div className="flex items-end gap-6">
                                <div className="stat-number font-display text-[10rem] text-transparent leading-none"
                                    style={{
                                        WebkitTextStroke: "2px var(--lime)",
                                        textStroke: "2px var(--lime)"
                                    }}>
                                    01
                                </div>
                                <div className="pb-6">
                                    <div className="text-[--lime] font-mono-tech text-sm uppercase tracking-wider">Product</div>
                                    <div className="text-[--silver] text-sm">In Market</div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </section>
    );
}
